.thumbnail-image {
    width: 100%;
    position: relative;

    img{
        width:100%;
        object-fit: contain;
    }

    .image-label{
        width:fit-content;
        padding: 5px 10px;
        z-index: 10;
        position: absolute;
    }

    .source{
        bottom: 5px;
        right: 5px;
    }

    .date-photo{
        right: 5px;
        top: 5px;
    }
}