.matchRate {
    .text-green{
        color: #57ab6a;
    }

    .text-orange{
        color: #ea873d;
    }

    .text-red{
        color: #d8271e;
    }

    .bg-grey{
        background-color: lightgrey;
        
        height: 2vw;
        //fix strange margin issues 
        margin-right: -1px;
        margin-top: -1px;
        .label{
            padding: 2px 0px 0px 4px;
        }
        
        .toggle-detail {
            margin-left: auto;
            text-decoration: underline;
            cursor: pointer;
            padding-right: 6px;
            padding-top: 2px;
        }
    }

    .bg-white{
        background-color: white;
        padding: 2px 6px 0px 6px;
    }

    .arrow-right {
        width: 0; 
        height: 0; 
        border-top: 1vw solid transparent;
        border-bottom: 1vw solid transparent;
        border-left: 1vw solid white;
    }

    
}

